.bg-color {
  padding: 0px 0px 15px 0px;
  background-color: aqua;
}

.bg-color a {
  color: white;
  text-decoration: unset;
}

.Header {
  background-color: aqua;
  background-image: url("./images/raw/Girls-Listen-Music_0.jpg");
  background-size: cover;
}

.header-opacity {
  background-color: rgba(0, 0, 0, 0.5);
  padding-top: 20%;
  padding-bottom: 20%;
}

.Browse {
  background-color: gray;
  padding: 15px;
  position: relative;
}

.Browse p {
  position: absolute;
  bottom: 1%;
  background-color: aqua;
  padding: 10px;
  width: max-content;
}

.ArtistPage {
  background-color: grey;
  padding-top: 3%;
}

.Artist-page-col-12 {
  padding: 40px;
}

.container-fluid {
  padding: 0;
}


.row {
  margin: 0;
}

.col-md-6 {
  padding: 0;
}

.ErrorPage {
  padding: 100px 0;
}

.ErrorPage h1 {
  font-size: 70px;
  color: #f95e6d;
}

.ErrorPage p {
  font-size: 20px;
}

.ErrorPage .link {
  color: #f95e6d;
}
